import { Union } from "../.fable/fable-library.3.2.2/Types.js";
import { string_type, union_type } from "../.fable/fable-library.3.2.2/Reflection.js";
import { Minute$reflection } from "../../Fluke.Shared/Domain/Model.fs.js";
import { View } from "../../Fluke.Shared/View.fs.js";
import { interpolate, toText } from "../.fable/fable-library.3.2.2/String.js";

export class Hover extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Cell", "Task", "Information", "None"];
    }
}

export function Hover$reflection() {
    return union_type("Fluke.UI.Frontend.TempUI.Hover", [], Hover, () => [[], [], [], []]);
}

export class DockType extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Databases", "Settings", "Database", "Information", "Task", "Cell"];
    }
}

export function DockType$reflection() {
    return union_type("Fluke.UI.Frontend.TempUI.DockType", [], DockType, () => [[], [], [], [], [], []]);
}

export class ActiveSession extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["ActiveSession"];
    }
}

export function ActiveSession$reflection() {
    return union_type("Fluke.UI.Frontend.TempUI.ActiveSession", [], ActiveSession, () => [[["taskName", string_type], ["duration", Minute$reflection()], ["totalDuration", Minute$reflection()], ["totalBreakDuration", Minute$reflection()]]]);
}

export const colorCornerBlue = "#005688";

export const colorCornerPink = "#a91c77";

export const defaultView = new View(0);

export function informationColor(_arg1) {
    switch (_arg1.tag) {
        case 1: {
            return "#666";
        }
        case 2: {
            return "#333";
        }
        case 3: {
            return toText(interpolate("[%P()]", [informationColor(_arg1.fields[0])]));
        }
        default: {
            return "#999";
        }
    }
}

export function manualCellStatusColor(_arg1) {
    if (_arg1.tag === 0) {
        return "#339933";
    }
    else if (_arg1.tag === 2) {
        return "#673ab7";
    }
    else if (_arg1.tag === 3) {
        return "#003038";
    }
    else if (_arg1.fields[0] != null) {
        return "#604800";
    }
    else {
        return "#b08200";
    }
}

export function cellStatusColor(_arg1) {
    switch (_arg1.tag) {
        case 1: {
            return "#4c664e";
        }
        case 2: {
            return "#262626";
        }
        case 3: {
            return "#990022";
        }
        case 4: {
            return "#530011";
        }
        case 5: {
            return manualCellStatusColor(_arg1.fields[1]);
        }
        default: {
            return "#595959";
        }
    }
}

