import { ofArray, map } from "../.fable/fable-library.3.2.2/List.js";

export function playDing() {
    void map((ms) => setTimeout(() => {
        (() => { var audio = new Audio("../sounds/ding.wav"); audio.volume = 0.5; return audio; })().play();;
    }, ms), ofArray([0, 1400]));
}

export function playTick() {
    (() => { var audio = new Audio("../sounds/tick.wav"); audio.volume = 0.5; return audio; })().play();;
}

