import { toList, ofList } from "../.fable/fable-library.3.2.2/Map.js";
import { map } from "../.fable/fable-library.3.2.2/List.js";
import { newGuid } from "../.fable/fable-library.3.2.2/Guid.js";
import { Task, TaskId } from "../../Fluke.Shared/Domain/Model.fs.js";
import { getDatabaseMap, templatesUser, databaseStateFromDslTemplate, DslTemplate, TemplateTask } from "../../Fluke.Shared/Templates.fs.js";
import { DatabaseId } from "../../Fluke.Shared/Domain/State.fs.js";

export function fetchTemplatesDatabaseStateMap() {
    return ofList(map((databaseState) => [databaseState.Database.Id, databaseState], map((tupledArg) => {
        const dslTemplate = tupledArg[1];
        const newDslTemplate = new DslTemplate(dslTemplate.Position, map((taskTemplate) => {
            let inputRecord;
            return new TemplateTask((inputRecord = taskTemplate.Task, new Task(new TaskId(0, newGuid()), inputRecord.Name, inputRecord.Information, inputRecord.Duration, inputRecord.PendingAfter, inputRecord.MissedAfter, inputRecord.Scheduling, inputRecord.Priority)), taskTemplate.Events, taskTemplate.Expected);
        }, dslTemplate.Tasks));
        return databaseStateFromDslTemplate(templatesUser, new DatabaseId(0, newGuid()), tupledArg[0], newDslTemplate);
    }, toList(getDatabaseMap(templatesUser)))));
}

