import { singleton } from "../fable-library.3.2.2/AsyncBuilder.js";
import { fold } from "../fable-library.3.2.2/Map.js";

export function Async_map(mapper, a) {
    return singleton.Delay(() => singleton.Bind(a, (_arg1) => singleton.Return(mapper(_arg1))));
}

export function Async_lift(value) {
    return singleton.Delay(() => singleton.Return(value));
}

export function Map_toJS(map) {
    return fold((jsMap, k, v) => jsMap.set(k, v), new Map(), map);
}

export function Promise_ignore(prom) {
    return prom.then(((value) => {
    }));
}

