import { getTicks, now } from "../../.fable/fable-library.3.2.2/Date.js";
import { op_Subtraction, fromNumber } from "../../.fable/fable-library.3.2.2/Long.js";
import { fromTicks } from "../../.fable/fable-library.3.2.2/TimeSpan.js";
import { set$ } from "./Dom.fs.js";
import { getItemFromDict } from "../../.fable/fable-library.3.2.2/MapUtil.js";

const initialTicks = (() => {
    let copyOfStruct = now();
    return getTicks(copyOfStruct);
})();

function ticksDiff(ticks) {
    let copyOfStruct;
    return fromNumber((copyOfStruct = fromTicks(op_Subtraction(ticks, initialTicks)), copyOfStruct), false);
}

export const profilingState = {
    CallCount: new Map([]),
    Timestamps: [],
};

set$("profilingState", profilingState);

export function addCount(id) {
    const matchValue = profilingState.CallCount.has(id);
    if (matchValue) {
        profilingState.CallCount.set(id, getItemFromDict(profilingState.CallCount, id) + 1);
    }
    else {
        profilingState.CallCount.set(id, 1);
    }
}

export function addTimestamp(id) {
    let copyOfStruct;
    void (profilingState.Timestamps.push([id, ticksDiff((copyOfStruct = now(), getTicks(copyOfStruct)))]));
}

addTimestamp("Init");

