import { link, Icons_externalLinkIcon } from "../Bindings/Chakra.fs.js";
import { equals } from "../../.fable/fable-library.3.2.2/Util.js";
import { Helpers_nothing } from "../../.fable/Fable.React.7.4.1/Fable.React.Helpers.fs.js";
import { createElement } from "react";
import * as react from "react";
import { Tooltip } from "./Tooltip.fs.js";

export const externalLinkIcon = Icons_externalLinkIcon()((x) => {
    x.marginLeft = "3px";
    x.marginTop = "-1px";
})([]);

export function ExternalLink(input) {
    const label = input.Href;
    const children = [link()((x) => {
        x.href = input.Href;
        x.isExternal = true;
        input.Props(x);
    })([input.Link, externalLinkIcon])];
    if (equals(label, Helpers_nothing)) {
        return react.createElement(react.Fragment, {}, ...children);
    }
    else {
        return createElement(Tooltip, {
            Children: children,
            Props: (x_1) => {
                x_1.label = label;
            },
        });
    }
}

