import jssha_1 from "jssha";
import { parse } from "../../.fable/fable-library.3.2.2/Guid.js";

export const jssha = jssha_1;

export function shake128(str) {
    const hash = new jssha("SHAKE128", "TEXT");
    hash.update(str);
    return hash.getHash("HEX", {
        outputLen: 128,
    });
}

export function getTextGuidHash(value) {
    return parse(shake128(value));
}

